import React from 'react';

import './_standings-selector.scss';

const StandingsSelect = (props)  =>
   (	 
		<div className="col col-sm-2 col-lg-1">
		   <select className="form-control mb-2" id="teamSelector" onChange={props.change}>
			<option value="All">All Teams</option>
					{props.data.map((team, index) => {
						return <option key={'key'+index} value={team}>{team}</option>
					})}
			</select>
		</div>
      );
             
export default StandingsSelect;


