/* global graphql */

const _ = require('lodash');
import React from 'react';
import Breadcrumb from '../components/breadcrumb';
import StandingsSelect from '../components/standings-selector';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import '../../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';

class IndexPage extends React.Component {
	constructor(props) {
		super(props);
		this.teamChangeHandler = this.teamChangeHandler.bind(this);
		this.loadData = this.loadData.bind(this);
		this.state = {
			teamsLabels: [],
			teamSelected: "",
			results: []

		}
		this.options = {
			defaultSortName: 'deltaSnapLast',  // default sort column name
			defaultSortOrder: 'desc'  // default sort order
		};
	}

	teamChangeHandler = (event) => {
		var selected = event.target.value;
		this.setState(
			{ teamSelected: selected },
			() => this.loadData()
		)
	}

	positionClickHandler = event => {
		this.loadData();

	}

	loadData = () => {

		var team = this.state.teamSelected;
		var rb = this.refs.rb.checked;
		var wr = this.refs.wr.checked;
		var te = this.refs.te.checked;
		var search = this.refs.search.value;
		const games = this.props.data.allDataJson.edges[0].node.games;
		const playerIds = this.state.playerIds;
		var allData = [];
		var formatedData = [];
		var results = [];
		var activeWeeks = ['Week 3', 'Week 4', 'Week 5', 'Week 6'];
		var player = {
			name: "",
			pos: "",
			team: "",
			weekA: "",
			weekB: "",
			weekC: "",
			weekD: "",
			deltaSnapLast: 0,
			deltaTargetLast: 0,
			deltaSnapAvg: 0,
			deltaTargetAvg: 0,
			averageSnap: 0,
			averageTarget: 0,
		}

		// get data for all the player ids that have data in the active weeks
		_.forEach(playerIds, (id) => {
			// take games based on player id
			var gamesPlayer = _.filter(games, ['playerId', id]);
			gamesPlayer = _.filter(gamesPlayer, (v) => _.includes(activeWeeks, v.week));
			if (!_.isEmpty(gamesPlayer)) {
				allData.push(gamesPlayer);
			}
		});

		_.forEach(allData, (games) => {
			
			var weekA = _.filter(games, ['week', 'Week 3'])[0];
			var weekB = _.filter(games, ['week', 'Week 4'])[0];
			var weekC = _.filter(games, ['week', 'Week 5'])[0];
			var weekD = _.filter(games, ['week', 'Week 6'])[0];

			player.averageSnap = Number(_.mean(_.map(games, 'offSnapPer')).toFixed(2));
			player.averageTarget = Number(_.mean(_.map(games, 'tarPer')).toFixed(2));

			player.weekA = `Snaps: ${_.get(weekA, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(weekA, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(weekA, 'utilization', 0), 0)}`;
			player.weekB = `Snaps: ${_.get(weekB, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(weekB, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(weekB, 'utilization', 0), 0)}`;
			player.weekC = `Snaps: ${_.get(weekC, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(weekC, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(weekC, 'utilization', 0), 0)}`;
			player.weekD = `Snaps: ${_.get(weekD, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(weekD, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(weekD, 'utilization', 0), 0)}`;

			var currentSnaps, futureSnaps, currentTar, futureTar;
			var tempDeltaSnapAvg = [];
			var tempDeltaTargetAvg = [];
			player.deltaSnapLast = 0;
			player.deltaTargetLast = 0;
			
			// take last 4 weeks of games 
			_.forEach(_.takeRight(games, 4), (game, index) => {
				player.name = game.name;
				player.pos = game.pos;
				player.team = game.team;
				currentSnaps = _.get(game, 'offSnapPer');
				futureSnaps = _.get(games[index + 1], 'offSnapPer');
				currentTar = _.get(game, 'tarPer');
				futureTar = _.get(games[index + 1], 'tarPer');

				if (index < (games.length - 1)
					&& currentSnaps != 0 && futureSnaps != 0) {
					tempDeltaSnapAvg.push(futureSnaps - currentSnaps);
					tempDeltaTargetAvg.push(futureTar - currentTar);
					if (game.week === "Week 5") {
						player.deltaSnapLast = parseFloat((futureSnaps - currentSnaps).toFixed(2)) ;
						player.deltaTargetLast = parseFloat((futureTar - currentTar).toFixed(2));
					}
				}

			});
			player.deltaTargetAvg = tempDeltaTargetAvg.length > 1 ? Number(_.mean(tempDeltaTargetAvg).toFixed(2)) : 0;
			player.deltaSnapAvg = tempDeltaSnapAvg.length > 1 ? Number(_.mean(tempDeltaSnapAvg).toFixed(2)) : 0;

			formatedData.push({
				name: player.name,
				pos: player.pos,
				team: player.team,
				weekA: player.weekA,
				weekB: player.weekB,
				weekC: player.weekC,
				weekD: player.weekD,
				deltaSnapLast: player.deltaSnapLast,
				deltaTargetLast: player.deltaTargetLast,
				deltaTargetAvg: player.deltaTargetAvg,
				deltaSnapAvg: player.deltaSnapAvg,
				averageSnap: player.averageSnap,
				averageTarget: player.averageTarget,
			});
		});

		var filterPos = [];

		if (te) filterPos.push('TE');
		if (wr) filterPos.push('WR');
		if (rb) filterPos.push('RB');

		// Final Results to print in table 
		formatedData.map((playerPrint) => {
			if (_.toLower(playerPrint.name).includes(_.toLower(search))) {
				if (_.some(filterPos, (el) => _.includes(playerPrint.pos, el))) {
					if (_.includes(playerPrint.team, team) || team === 'All') {
						results.push(playerPrint);
					}
				}
			}
		});

		this.setState(
			{ results: results }
		)
	}

	UNSAFE_componentWillMount() {
		const games = this.props.data.allDataJson.edges[0].node.games;
		const teamsUnique = _.uniqBy(games, 'team');
		const idUnique = _.uniqBy(games, 'playerId');
		var teamNames = [];
		teamsUnique.map((game) => {
			teamNames.push(game.team);
		});
		var playerIds = [];
		idUnique.map((game) => {
			playerIds.push(game.playerId);
		});

		teamNames.sort();
		this.setState({ teamNames: teamNames });
		this.setState({ playerIds: playerIds });
	}

	componentDidMount() {
		this.loadData()
	}

	render() {

		return (

			<main className="container-fluid">
				<div className="card mb-3">
					<div className="card-header">
						<form>
							<div className="form-row align-items-center">
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="rb" ref="rb" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >RB</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="wr" ref="wr" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >WR</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="te" ref="te" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >TE</label>
									</div>
								</div>
								<StandingsSelect data={this.state.teamNames} change={(event) => this.teamChangeHandler(event)} />
								<div className="col-sm-4 col-md-3">
									<div className="input-group mb-2">
										<input className="form-control" type="text" id="search" placeholder="Player name" ref="search" onChange={this.positionClickHandler} />
									</div>
								</div>
							</div>
						</form>

					</div>
					<div className="card-body">
						<div className="table-responsive">

							<BootstrapTable data={this.state.results} options={this.options} striped hover version='4'>
								<TableHeaderColumn isKey dataField='name' isKey={true} dataSort={true} tdStyle={{ whiteSpace: 'pre-wrap', verticalAlign: 'middle' }} width="13%" >Name</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='pos' dataSort={true} width="5%">Pos</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='team' dataSort={true} width="5%" >Tm</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal', textAlign: "center" }} dataField='weekA' width="12%" >Week 3</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal', textAlign: "center" }} dataField='weekB' width="12%">Week 4</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal', textAlign: "center" }} dataField='weekC' width="12%">Week 5</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal', textAlign: "center" }} dataField='weekD' width="12%">Week 6</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='deltaSnapLast' dataSort={true} width="8%" >Snap % &Delta;</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='deltaSnapAvg' dataSort={true} width="10%" >Snap % &Delta; Avg</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='averageSnap' dataSort={true} width="9%" >Snap % Avg</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='deltaTargetLast' dataSort={true} width="9%" >Target % &Delta;</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='deltaTargetAvg' dataSort={true} width="11%" >Target % &Delta; Avg</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='averageTarget' dataSort={true} width="10%" >Target % Avg</TableHeaderColumn>
							</BootstrapTable>
						</div>
					</div>
					<div className="card-footer small text-muted"></div>
				</div>
			</main>);
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query standingsQuery {
		allDataJson {
			edges {
				node {
					games {
						week
						name
						pos
						player
						offSnap
						offSnapPer
						team
						cmp
						att
						rush
						tar
						playerId
						tarPer
						rec
						rushYds
						rushTd
						rushLong
						recievingYds
						recievingTds
						recievingLong
						fumbles
						fumbleLost
						utilization
					}
				} 
			}
		}
}
`;



